import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import logo from './img/logo512.png'
import gordon from './img/gordon.jpg'
import bob_ross from './img/bob_ross.jpg'
import tony_fantano from './img/tony_fantano.jpeg'
import linkedin from './img/linkedin.png'
import github from './img/github.png'
import email from './img/email.png'
import peb from './img/icone_p_b.png'


class Review extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      text: ""
    }
    this.onChangeText = this.onChangeText.bind(this)
  }
  render() {
    return (
      <form className="review-box" onSubmit={async (e) => { e.preventDefault(); await this.props.onSubmit(this.state.text) }} onSubmitCapture={(e) => { }}>
        <textarea
          onChange={this.onChangeText}
          className="review-input-box"
          value={this.state.text}
        />
        <input
          type="submit"
          className="review-input-button"
          title="Send"
          onSubmitCapture={(e) => { }}
          onSubmit={(e) => { }}
        />
      </form>
    )
  }
  onChangeText(evt) {
    this.setState({ text: evt.target.value })
  }
}

class FirstPage extends React.Component {
  render() {
    return (
      <div className="group">
        <div className="title">
          Let we know your review and we'll show you how evil it is!
          </div>
        <Review onSubmit={this.props.onSubmitReview} />
      </div>
    )
  }
}

class SecondPage extends React.Component {
  render() {
    return (
      <div className='resultScreen'>
        <div className='result'>
          <img src={this.props.resultImg} alt='alt' />
          <div className='person'>
            {this.props.person}
          </div>
          <div className='description'>
            {this.props.description}
          </div>
        </div>
        <button
          className='try-again-button'
          onClick={this.props.onClickTryAgain}> Try again! </button>
      </div>
    )
  }
}

let FOOTEIRU = (props) => (<div className='footer'>
  <a href="https://www.github.com/ptcar2009">
    <img alt='img' src={github} />
    github.com/ptcar2009
  </a>
  <a href="https://www.linkedin.com/in/pteco-dev">
    <img alt='img' src={linkedin} />
    Pedro Carvalho
  </a>
  <a href="mailto:ptcar2009@gmail.com">
    <img alt='img' src={email} />
    ptcar@pteco.dev
  </a>
  <a href="https://www.pteco.dev">
    <img alt='img' src={peb} />
    pteco.dev
  </a>
</div>)
let api = "https://o8tab5nprh.execute-api.us-east-1.amazonaws.com/test"

class Main extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      hasSubmitted: false,
      person: null,
      description: null,
      resultImg: null,
    }
    this.onSubmitReview = this.onSubmitReview.bind(this)

  }
  render() {
    return (
      <div className="page">
        <a href='https://www.pteco.dev' className="logo">
          <img src={logo} alt="alt" className='logo' />
        </a>
        {!this.state.hasSubmitted
          ? <FirstPage onSubmitReview={this.onSubmitReview} />
          : <SecondPage onClickTryAgain={(ev) => {
            this.setState({ reviewValue: null, hasSubmitted: false })
          }}
            resultImg={this.state.resultImg}
            description={this.state.description}
            person={this.state.person} />}
        <FOOTEIRU />
      </div>
    )
  }
  onResponseReceived(result) {
    if (result > 0.7) {
      this.setState({
        person: "Bob Ross",
        description: "So lightweigh and easy going.",
        resultImg: bob_ross,
        hasSubmitted: true
      })
    }
    else if (result > 0.3) {
      this.setState({
        person: "Tony Fantano",
        description: "Very just, but very strict.",
        resultImg: tony_fantano,
        hasSubmitted: true
      })
    }
    else {
      this.setState({
        person: "Gordon Ramsay",
        description: "Gosh Freaking Darn It.",
        resultImg: gordon,
        hasSubmitted: true
      })
    }

  }
  async onSubmitReview(e) {
    if (e === '') {
      return
    }
    let response
    try {
      response = await fetch(api, {
        method: 'POST',
        headers: {
          'Content-Type': 'text/plain'
        },
        body: e,
      })
    } catch (e) {
      console.log(e)
    }
    if (response.ok) {
      this.onResponseReceived(await response.text())
    }
  }

}

// ========================================

ReactDOM.render(<Main />, document.getElementById("root"));
